<template>
    <li class="user-preview">
        <h2>{{user.username}}</h2>
         <!-- <button @click="goToEdit(toy._id)">Edit</button> -->
         <button @click="showUserDetails(user._id)">Details</button>
          <button @click="emitDelete(user._id)">X</button>
    </li>
</template>


<script>
export default {
    name: "user-preview",
    props: {
        user: Object,
    },
    methods: {
        // goToEdit(toyId){
        //      this.$router.push(`/edit/${toyId}`);
        // },
        showUserDetails(userId) {
             this.$router.push(`/user/details/${userId}`);
        },
        emitDelete(userId) {
            console.log(userId);
            this.$emit('deleteUser', userId)
        }
    },
};
</script>
