<template>
    <section class="login-page">
        <h1>Please Login First</h1>
        <form @submit.prevent="loginUser">
            <input type="text" class="input" v-model="userCred.username" placeholder="Enter Username" />
            <input type="text" class="input" v-model="userCred.password" placeholder="Password" />
            <input type="email"  v-model="userCred.email" placeholder="Email" />
            <button>Login</button>
        </form>
    </section>
</template>


<script>
import toyList from '../cmps/toy-list.vue'

export default {
    name: "login-page",
    data() {
       return {
           userCred: {
                username: '',
                email: '',
                password: null
           }
           
        }
    },
    methods: {
      loginUser(){
          this.$store.dispatch({type: 'login', userCred: this.userCred})
          this.$router.push("/")
      }
    },
    computed: {
        
    },
    created() {
        
    },
    components: {
      
    },
};
</script>
