<template>
    <section v-if="toy">
        <h2>{{ toy.name }}</h2>
        <h3>Price: {{ toy.price }}</h3>
        <h3>Type: {{ toy.type }}</h3>
        <h3 class="inStock" v-if="toy.inStock">In stock</h3>
        <h3 class="outOfStock" v-else>Out of stock</h3>
         <form @submit.prevent="addReview">
            <textarea placeholder="Give us review" v-model="reviewToEdit.txt" ></textarea>
            <button>Send</button>
        </form>
        <p>{{toy}}</p>
        <button @click="goBack" >Back</button>
    </section>
</template>

<script>
import { toyService } from "../../services/toy-service.js";

export default {
    data() {
        return {
            toy: null,
            reviewToEdit: {
                txt: '',
                aboutToyId: null,
            },
        };
    },
    methods: {
        goBack() {
             this.$router.push(`/`);
        },
        addReview() {
            console.log(this.reviewToEdit);
            this.reviewToEdit.aboutToyId = this.toy._id
            this.$store.dispatch({type: 'addReview', review: this.reviewToEdit})
            this.reviewToEdit = {txt: '', aboutUserId: null}
            this.$store.dispatch({type: 'loadReviews'})
        }
    },
    created() {
        const toyId = this.$route.params.id;
        toyService.getById(toyId).then((toy) => (this.toy = toy));
    },
}
</script>
