<template>
    <li class="toy-preview">
        <h2>{{toy.name}}</h2>
        <!-- <h2>{{toy}}</h2> -->
        <img v-if="toy.imgUrl" :src="toy.imgUrl" alt="">
         <button @click="showDetails(toy._id)">Details</button>
         <button @click="goToEdit(toy._id)">Edit</button>
          <button @click="emitDelete(toy._id)">X</button>
    </li>
</template>


<script>
export default {
    name: "toy-preview",
    props: {
        toy: Object,
    },
    methods: {
        goToEdit(toyId){
             this.$router.push(`/edit/${toyId}`);
        },
        showDetails(toyId) {
             this.$router.push(`/details/${toyId}`);
        },
        emitDelete(toyId) {
            this.$emit('deleteToy', toyId)
        }
    },
};
</script>
