<template>
    <section v-if="toy" class="toy-edit">
    <!-- <section class="toy-edit"> -->
        <h1>todo edit</h1>
        <form @submit.prevent="saveToy">
            <img class="toy-img" :src="toy.imgUrl">
            <!-- <img class="toy-img" src="https://res.cloudinary.com/gabbay/image/upload/v1606059825/train_o4u7lw.jpg"> -->
            <h3>Name</h3>
            <input type="text" v-model="toy.name" placeholder="Enter Toy Name">
            <h3>Type</h3>
            <select class="edit-select" v-model="toy.type">
                <option value="Funny">Funny</option>
                <option value="Adult">Adult</option>
                <option value="Educational">Educational</option>
            </select>
            <h3>Price</h3>
            <input type="number" v-model="toy.price" placeholder="Enter Price">
            <label>
                In stock?
                <input type="checkbox" v-model="toy.inStock">
            </label>
            <template v-if="!isLoading">
                <!-- <label for="imgUploader"> <img class="toy-img" src="toy.imgUrl"> </label> -->
                <input class="file" type="file" name="img-uploader" id="imgUploader" @change="onUploadImg">
            </template>
            <h3 v-else>loading...</h3>
        <button>Save</button>
        <button @click="goBack" >Back</button>
        </form>
    </section>
</template>

<script>
import { toyService } from "../../services/toy-service.js";
import {uploadImg} from '../../services/img-upload-service.js'

export default {
    name: 'toy-edit',
    data() {
        return {
            toy: null,
            isLoading: false,
            imgUrls: []
        };
    },
    computed: {
       
    },
    methods: {
        goBack() {
            this.$router.push("/");
        },
        saveToy(){
            console.log(this.toy);
            if (this.toy._id) {
                this.$store.dispatch({type: 'editToy', toy: this.toy})
            } else {
                 this.$store.dispatch({type: 'addToy', toy: this.toy})
            }
              this.$router.push("/");
        },
         async onUploadImg(ev) {
             this.isLoading = true;
             const res = await uploadImg(ev)
              this.imgUrls.push(res.url)
               this.toy.imgUrl = res.url;
               console.log('this.toy.imgUrl ', this.toy.imgUrl );
            //    this.$store.dispatch({type: "addImg", toy: this.toy})
              this.isLoading = false;
        }
       
    },
    created() {
        if (!this.$route.params.id) {
            this.$store.dispatch('getEmpty')
                .then(emptyToy => this.toy = emptyToy)
        } else {
            const toyId = this.$route.params.id;
            toyService.getById(toyId).then((toy) => {
                this.toy = JSON.parse(JSON.stringify(toy));
            });
        }
    },
};
</script>
