<template>
    <section v-if="users" class="toy-list">
        <button v-if="!usersShows" @click="loadUsers" >Show Users</button>
        <button v-if="usersShows" @click="hideUsers" >Hide Users</button>
        <ul  v-if="usersShows">
            <li v-for = "user in users" :key="user.id" >
                 <user-preview :user="user" @deleteUser="emitDelete"/>
                <!-- {{user}} -->
            </li>
        </ul>
    </section>
</template> 


<script>
import userPreview from "../cmps/user-preview.vue";

export default {
    name: "user-list",
    data() {
        return {
            usersShows: false
        }
    },
    props: {
        users: Array,
    },
    methods: {
      loadUsers(){
          this.usersShows = !this.usersShows
           this.$emit('loadUsers')
      },
      hideUsers() {
          this.usersShows = false
      },
       emitDelete(userId){
           this.$emit('deleteUser', userId)
      }
    },
    components: {
        userPreview,
    },
    created(){
       
    }
};
</script>
