<template>
    <section class="toy-app">
       <h1>Toys</h1>
        <button v-if="loginUser" @click="dispatchLogout">Logout</button>
        <button v-if="!loginUser" @click="login">Login</button>
       <h2 v-if="loginUser"> User: {{loginUser.username}}</h2>
       <user-list v-if="loginUser" @loadUsers="dispatchUsers" :users="users" @deleteUser="dispatchDeleteUser" />
        <button @click="addToy">Add Toy</button>
       <toy-list :toys="toys" @deleteToy="dispatchDelete"/> 
    </section>
</template>


<script>
import toyList from '../cmps/toy-list.vue'
import userList from '../cmps/user-list.vue'

export default {
    name: "toy-app",
    methods: {
       dispatchDelete(toyId) {
           this.$store.dispatch({
               type: 'deleteToy',
                toyId,
           })
       },
       dispatchDeleteUser(userId) {
           this.$store.dispatch({
               type: 'removeUser',
                userId,
           })
       },
       addToy() {
            this.$router.push("/edit");
       },
        dispatchLogout() {
            this.$store.dispatch({
               type: 'logout',
           })
        },
        login() {
            this.$router.push("/login")
        },
        dispatchUsers() {
            this.$store.dispatch({
                type: 'loadUsers'
            })
        }
        
    },
    computed: {
        toys() {
            return this.$store.getters.toys;
        },
        loginUser() {
            return this.$store.getters.loggedinUser
        },
        users() {
             return this.$store.getters.users
        }
    },
    created() {
          this.$store.dispatch({
            type: "loadToys",
        });
    },
    components: {
      toyList,
      userList
    },
};
</script>
